.reg-form
{
	padding: 83px;
	padding-bottom: 25px;
}
.reg-form__logo
{
	margin-bottom: 85px;
}
.reg-form__title
{
	font-family: $bold;
	font-size: 36px;
	line-height: 42px;
	letter-spacing: -0.03em;
	color: $dark-blue;
	margin-bottom: 29px;
}
.reg-form__descr
{
	font-family: $regular;
	font-size: 18px;
	line-height: 25px;
	color: $dark-blue;
	margin-bottom: 40px;
}
.reg-form__form
{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
}
.reg-form__wr
{
	max-width: 554px;
	width: 100%;
	margin-bottom: 20px;
}
.reg-form__form-label
{
	position: relative;
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-direction: column;
	background-color: #FFFFFF;
	border: 1px solid #C1BBBB;
	padding-left: 27px;
	padding-top: 12px;
	width: 100%;
	font-size: 18px;
	line-height: 21px;
	color: rgba(0, 0, 0, 0.61);
	&:not(:first-child)
	{
		border-top: none;
	}
}
.reg-form__form-input
{
	outline: none;
	border: none;
	width: 90%;
	padding: 12px 27px;
	padding-left: 0;
	font-family: $semi;
	font-size: 18px;
	line-height: 21px;
	color: #FFAE00;
	&:focus
	{

		&~span:after
		{
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 3px;
			height: 100%;
			background-color: #FFAE00;
		}
	}
}
.reg-form__form-input::placeholder
{
	font-family: $semi;
	font-size: 18px;
	line-height: 21px;
	color: #FFAE00;
}
.reg-form__form-check-wr
{
	display: flex;
	align-items: center;
	justify-content: flex-start;
	margin-bottom: 40px;
}
.reg-form__form-checkbox
{
	margin-right: 12px;
}
.reg-form__form-checkbox-label
{
	font-size: 18px;
	line-height: 25px;
	color: #334D6E;
}
.reg-form__form-submit
{
	padding: 14px 64px;
	color: #fff;
	background: #FFAE00;
	border-radius: 2px;
	border: none;
	outline: none;
	font-family: $semi;
	font-size: 18px;
	line-height: 25px;
	text-align: center;
	letter-spacing: 0.03em;
}